import {SearchGameVideosRequest, SearchGameVideosResponse} from "./searchGamesModel";

export type ISearchGamesAction = ISearchGamesAsyncAction |
  ISearchGamesResultAction;

export enum SearchGamesAction {
  SearchGamesAsync = "SearchGamesAsync",
  SearchGamesResponse = "SearchGamesResponse"
}

export interface ISearchGamesAsyncAction {
  type: SearchGamesAction.SearchGamesAsync,
  request: SearchGameVideosRequest
}

export interface ISearchGamesResultAction {
  type: SearchGamesAction.SearchGamesResponse,
  response: SearchGameVideosResponse
}

export function searchGames(request: SearchGameVideosRequest): ISearchGamesAsyncAction {
  return {
    type: SearchGamesAction.SearchGamesAsync,
    request
  }
}

export function searchGamesResponse(response: SearchGameVideosResponse): ISearchGamesResultAction {
  return {
    type: SearchGamesAction.SearchGamesResponse,
    response
  }
}
