import React from 'react';

import './SearchGamesPage.css';
import {SearchGameVideosRequest, SearchGameVideosResponse} from "../../../../state/lol/games/searchGamesModel";
import GameVideos from "../../../GameVideos/GameVideos";

interface ISearchGamesPageProps {
  request: SearchGameVideosRequest
  response?: SearchGameVideosResponse
}

export class SearchGamesPage extends React.Component<ISearchGamesPageProps> {
  public render() {
    return (
      <div>
        {this.getPageContent()}
      </div>
    );
  }

  private getPageContent() {
    const response = this.props.response;
    if (!response) {
      return "...loading";
    }

    if (response && response.gameVideos.length === 0) {
      return "none found";
    }

    return (
      <GameVideos gameVideos={response.gameVideos}/>
    );
  }
}

export default SearchGamesPage;
