import {SearchGamesAction, ISearchGamesAction} from "./gamesActions";
import {SearchGameVideosResponse} from "./searchGamesModel";

export interface ISearchGamesState {
  response?: SearchGameVideosResponse
}

export const INITIAL_STATE: ISearchGamesState = {
  response: undefined
};

export function searchGamesReducer(
  state: ISearchGamesState = INITIAL_STATE,
  action: ISearchGamesAction
): ISearchGamesState {
  switch (action.type) {
    case SearchGamesAction.SearchGamesAsync: {
      return state;
    }

    case SearchGamesAction.SearchGamesResponse: {
      return {
        response: action.response
      };
    }

    default: {
      return state;
    }
  }
}
