import { withSearchGames } from "../Pages/LoL/SearchGamesPage/withSearchGames";
import SearchGamesPage from "../Pages/LoL/SearchGamesPage/SearchGamesPage";

export enum PageId {
  Hello = "Hello",
  SearchLolGames = "SearchLolGames"
}

export interface IPageRoute {
  component: React.ComponentClass,
  id: PageId,
  path: string,
  title: string
}

export class PageRoutes {
  public static SearchLolGames: IPageRoute = {
    component: withSearchGames(SearchGamesPage),
    id: PageId.SearchLolGames,
    path: "/lol/games/streamer/:twitchLogin/summoner/:summonerName/champion/:championName",
    title: "Search For LoL Games"
  }
}
