import {SearchGameVideosRequest, SearchGameVideosResponse} from "./searchGamesModel";
import config from "../../../config/index"

export function searchGames(request: SearchGameVideosRequest): Promise<SearchGameVideosResponse> {
  // /games/streamer/:twitchLogin/summoner/:summonerName/champion/:championName
  const url = `${config.api.endpoint}/lol/games/streamer/${request.twitchLogin}/summoner/${request.summonerName}/champion/${request.championName}`;
  const responsePromise = fetch(url);
  return responsePromise
    .then((res: Response) => {
      return res.json();
    }).then((resultJson: any) => {
      return resultJson as SearchGameVideosResponse;
    })
    .catch((error: any) => {
      console.error("searchGames failed" + JSON.stringify(error));
      throw "searchGames failed";
    });
}
