import { call, takeEvery, put } from "redux-saga/effects";

import { SearchGameVideosResponse } from "./searchGamesModel";
import {SearchGamesAction, ISearchGamesAsyncAction, searchGamesResponse} from "./gamesActions";
import { searchGames } from "./gamesApi";

export function* SearchGamesAsync(action: ISearchGamesAsyncAction): IterableIterator<any> {
  const response: SearchGameVideosResponse = yield call(searchGames, action.request);
  yield put(searchGamesResponse(response))
}

export function* watchSearchGamesAsync() {
  yield takeEvery(
    SearchGamesAction.SearchGamesAsync,
    SearchGamesAsync
  )
}
