import React from 'react';
import moment from "moment";
import { Card, Col, Row } from 'antd';

import { GameVideo, Video } from "../../state/lol/games/searchGamesModel";



interface IGameVideosProps {
  gameVideos: GameVideo[]
}

class GameVideos extends React.Component<IGameVideosProps> {
  public render() {
    const { gameVideos } = this.props;

    const cards = gameVideos.map(gameVideo => {
      const title = (
        <span>{gameVideo.video.title} | {gameVideo.summoner.name} as {gameVideo.champion.name}</span>
      );
      const colKey = gameVideo.game.id + "-" + gameVideo.video.id;
      const gameStartTime = moment(gameVideo.game.startTimestamp * 1000).calendar();
      return (
        <Col span={8} key={colKey} data-game-id={gameVideo.game.id} data-twitch-video-id={gameVideo.video.id}>
          <Card title={title} bordered={false}>
            <a href={gameVideo.video.url} target={"_blank"}>
              <img
                src={this.getVideoThumbnail(gameVideo.video)}
                style={{ width: "100%" }}
              />
            </a>

            <span>
              {gameVideo.summoner.name} playing {gameVideo.champion.name}
            </span>

            <br />

            <span>
              {gameStartTime}
            </span>
          </Card>
        </Col>
      )
    });

    return (
      <div style={{ padding: "30px" }}>
        <Row gutter={8}>
          {cards}
        </Row>
      </div>
    );
  }

  private getVideoThumbnail(video: Video): string {
    return video.thumbnailImageUrl.replace("%{width}", "800").replace("%{height}", "450");
  }
}

export default GameVideos;
