import React from "react";

import { RouteComponentProps } from "react-router";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";

import { ISearchGamesState } from "../../../../state/lol/games/gamesReducer";
import { searchGames } from "../../../../state/lol/games/gamesActions";
import {SearchGameVideosRequest, SearchGameVideosResponse} from "../../../../state/lol/games/searchGamesModel";

export interface IWithSearchGamesProps {
  response?: SearchGameVideosResponse,
  request: SearchGameVideosRequest
}

interface IDispatchProps {
  searchGames: (request: SearchGameVideosRequest) => void;
}

interface IWithSearchGamesHocProps
  extends RouteComponentProps<{twitchLogin: string, summonerName: string, championName: string}>,
    IDispatchProps,
    IWithSearchGamesProps {

}

export function withSearchGames<T extends IWithSearchGamesProps>(Wrapped: React.ComponentType<T>): any {
  const hocClass = class extends React.Component<IWithSearchGamesHocProps> {
    state = {
      searchGamesTimer: undefined
    };

    public render() {
      const wrappedProps: T = {
        request: {
          summonerName: this.props.match.params.summonerName,
          twitchLogin: this.props.match.params.twitchLogin,
          championName: this.props.match.params.championName
        },
        response: this.props.response
      } as T;
      return (<Wrapped {...wrappedProps} /> );
    }

    public componentDidMount() {
      this.searchGames();
    }

    private searchGames() {
      this.props.searchGames({
        summonerName: this.props.match.params.summonerName,
        twitchLogin: this.props.match.params.twitchLogin,
        championName: this.props.match.params.championName
      });
    }

    public componentDidUpdate(prevProps: IWithSearchGamesHocProps) {
      if (
        (prevProps.match.params.summonerName !== this.props.match.params.summonerName) ||
        (prevProps.match.params.championName !== this.props.match.params.championName) ||
        (prevProps.match.params.twitchLogin !== this.props.match.params.twitchLogin)
      ) {
        this.searchGames();
      }
    }
  };

  const connectedHoc = connect(mapStateToProps, mapDispatchToProps)(hocClass);
  return withRouter(connectedHoc);
}

interface IWithSearchGamesState {
  searchGames: ISearchGamesState
}

export function mapStateToProps(state: IWithSearchGamesState, ownProps: IWithSearchGamesHocProps): IWithSearchGamesProps {
  return {
    request: ownProps.request,
    response: state.searchGames.response
  }
}

export function mapDispatchToProps(dispatch: any): IDispatchProps {
  return {
    searchGames: request => dispatch(searchGames(request))
  };
}
